import React, { useEffect, useState } from 'react'
import { connect } from "react-redux"
// import { Row, Col } from 'antd';

import { LayoutLottery, TitleBox } from "components";
import { MyPageForm } from 'moduls'
import "./style.scss";
// import { isArray } from 'lodash'



const MyAccount = ({
    onGetAllBanks,
    getUserInfo,
}) => {

    useEffect(() => {
        onGetAllBanks()
        getUserInfo()
    }, [])
    return (
        <LayoutLottery>
            <div id="my-page" className="page-register container-cst">
                <TitleBox name="마이페이지" />
                <MyPageForm />
            </div>
        </LayoutLottery>
    )
}

export default connect(null,
    ({
        common: { onGetAllBanks },
        auth: { getUserInfo }
    }) => {
        return {
            onGetAllBanks,
            getUserInfo,
        };
    }
)(MyAccount);
